@font-face {
    font-family: 'TTLakes';
    src: url(./fonts/TTLakes/TTLakes-Regular.eot) format('eot'),
    url(./fonts/TTLakes/TTLakes-Regular.ttf) format('ttf'),
    url(./fonts/TTLakes/TTLakes-Regular.woff) format('woff');
    font-weight: 400;
    font-style: normal;
  }
  @font-face {
    font-family: 'TTLakes';
    src: url(./fonts/TTLakes/TTLakes-Medium.eot) format('eot'),
    url(./fonts/TTLakes/TTLakes-Medium.ttf) format('ttf'),
    url(./fonts/TTLakes/TTLakes-Medium.woff) format('woff');
    font-weight: 500;
    font-style: normal;
  }
  @font-face {
    font-family: 'TTLakes';
    src: url(./fonts/TTLakes/TTLakes-Bold.eot) format('eot'),
    url(./fonts/TTLakes/TTLakes-Bold.ttf) format('ttf'),
    url(./fonts/TTLakes/TTLakes-Bold.woff) format('woff');
    font-weight: 700;
    font-style: normal;
  }